@import "./App.css";
html {
	scroll-behavior: smooth;
}
html {
	box-sizing: border-box;
	font-size: 62.5%;
}
body {
	font-size: 16px;
	margin: 0;
	font-family: var(--fuente-parrafo);
	background-color: var(--color-06);
}
*,
*::before,
*::after {
	box-sizing: inherit;
	margin: 0;
	padding: 0;
}
a {
	text-decoration: none;
}

input {
	border: none;
}
